.top-sec-title {
  font-size: 3.7vw;
  font-weight: 100;
}

.top-viewmore {
  color: #fff;
  font-family: 'Prompt', sans-serif;
  font-size: 1.6vw;
  border-radius: 40px;
  padding: 1.3vw 2.5vw;
  display: inline-block;
}

.top-about {
  color: #fff;
  text-align: center;
  background-image: url(../img/top/about-back.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.top-about:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(35, 24, 21, 0.2);
  z-index: 1;
}

.top-about .top-container {
  z-index: 2;
  position: relative;
}

.top-about .top-container .top-about__text {
  font-family: "ten-mincho-text", serif;
  line-height: 1.8;
  padding: 5vw 0;
}

.top-about .top-container .top-about__text .top-about__text__en {
  font-size: 3vw;
  font-weight: 100;
}

.top-about .top-container .top-about__text .top-about__text__ja {
  font-size: 2.6vw;
  font-weight: 100;
}

.top-about .top-container .top-about__btn {
  display: flex;
  justify-content: flex-end;
}

.top-about .top-container .top-about__btn .top-aout__viewmore {
  background-image: radial-gradient(#94F990, #27D5DA);
  background-image: -webkit-radial-gradient(#94F990, #27D5DA);
}
